import React from 'react';
// import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import {Grid, Paper, Button, Typography} from '@material-ui/core';
import { Formik, Form, Field, ErrorMessage} from 'formik';
import * as Yup from 'yup';


const ContactForm= () => {
    const paperStyle = { padding: '20px 20px', width: 550, margin: '0px auto', borderRadius: 20}
    const btnStyle = {marginTop: 10, width: "140px", height: "50px", background:"teal" ,color:"white",fontSize:"18px", fontWeight:"bold"}
    const initialValues = {
        firstName: '',
        lastName: '',
        companyName: '',
        phoneNum: '',
        email: '',
        message:''
    }
    const validationSchema=Yup.object().shape({
            firstName:Yup.string().required("Required"),
            lastName:Yup.string().required("Required"),
            companyName:Yup.string().required("Required"),
            phoneNum:Yup.number().typeError("Enter valid Phone Number").required("Required"),
            email:Yup.string().email("Enter valid email").required("Required"),
            message:Yup.string().required("Required")
    })
    const onSubmit =(values,props)=> {
        console.log(values,props)
        // alert(JSON.stringify(values),null,2)
        // props.resetForm()
    }
  return (
    <Paper elevation = {5} style = {paperStyle}>
   <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit}>
       {(props)=>(
    <Form id="contactForm"  noValidate autoComplete="off">
        
    <div style={{maxWidth:500, margin:"10px auto", padding:"10px 30px"}}>
        <Typography style ={{marginTop: "5px", textAlign:"left", fontWeight:"bolder"} } gutterBottom variant="h3" color="initial">Contact Us</Typography>
        <Typography style ={{marginBottom: "15px", textAlign:"left"} } gutterBottom variant="h5" color="initial">We want to hear from you!</Typography>
        </div>
      <Grid container spacing={1}>
            <Grid xs={12} sm={6} item>
            <Field as={TextField}style ={{borderRadius:"60px"}} name="firstName" type="text" id="firstName" label= 'First Name' placeholder="Enter First Name" variant="outlined" fullWidth value={props.values.firstName} onChange={props.handleChange} required helperText={<ErrorMessage name="firstName"/>} error={props.errors.firstName&&props.touched.firstName} /><br/>
            </Grid>
            <Grid xs={12} sm={6} item>
              <Field as={TextField} type="text" name="lastName" id="lastName" label='Last Name' placeholder="Enter Last Name" variant="outlined" fullWidth value={props.values.lastName} onChange={props.handleChange}  helperText={<ErrorMessage name="lastName"/>} required error={props.errors.lastName&&props.touched.lastName} /><br />
            </Grid>
            <Grid xs={12} sm={12} item>
              <Field as={TextField} id="companyName" name="companyName" label='Company Name' placeholder="Enter Company's Name" variant="outlined" fullWidth value={props.values.companyName} onChange={props.handleChange}  helperText={<ErrorMessage name="companyName" />} required error={props.errors.companyName&&props.touched.companyName}  /><br />
            </Grid>
            <Grid xs={12} sm={6} item>
              <Field as={TextField} id="phoneNum" name="phoneNum" type="tel" label='Phone Number' placeholder="Enter Phone Number" variant="outlined" fullWidth  helperText={<ErrorMessage name="phoneNum" />} required error={props.errors.phoneNum&&props.touched.phoneNum}  /><br />
            </Grid>
            <Grid xs={12} sm={6} item>
              <Field as={TextField} name="email" id="email"  type="email" label='Email' placeholder="Enter Email" variant="outlined" fullWidth value={props.values.email} onChange={props.handleChange}  helperText={<ErrorMessage name="email" />} error={props.errors.email&&props.touched.email}  required /><br />
            </Grid>
            <Grid xs={12} sm={12} item>
              <Field as={TextField} name="message" id="message" type="text" multiline rows={4} label='Message' variant="outlined" placeholder="How may we assist you?" fullWidth value={props.values.message} onChange={props.handleChange} helperText={<ErrorMessage name="message" />} error={props.errors.message&&props.touched.message} required /><br />
            </Grid>
            <Button type="submit" id="submitBtn" style={btnStyle} variant="contained">Submit</Button>
       </Grid>
    </Form>)}
   </Formik>
</Paper>
    

  )
}

export default ContactForm;
